ul {
    padding: 0;
}

li {
    list-style: none;
}

.resume-container {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

a {
    text-decoration: none;
    color: black;
}

h1 {
    margin-top: 0;
}

.my-photo img, .my-photo {
    max-width: 200px;
    max-height: 200px;
    width: 100%;
    height: 100%;
}

.contacts-icons {
    max-width: 20px;
    max-height: 20px;
}

.contacts a{
    display: flex;
    margin: 10px 0;
    transition: .5s;
}

.contacts img {
    padding-right: 10px;
}

.contacts a:hover {
    color: gray;
    transition: .5s;
}

@media (max-width: 600px) {
    .right-side {
      display: none;
    }
  }