.navbar-container {
    display: flex;
    max-width: 600px;
    margin: 20px auto;
    justify-content: space-around;
    border: 1px solid black;
    border-radius: 10px;
}

.navbar-button {
    padding: 10px 0;
    cursor: pointer;
    border-bottom: 1px solid white;
    transition: .5s;
}

.navbar-button a:hover {
    color: gray;
    transition: .5s;
    border-bottom: 1px solid gray;
}